'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { RequestData } from '@/lib/database'

export default function AdminDashboard() {
  const [requests, setRequests] = useState<RequestData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [lastRequestCount, setLastRequestCount] = useState(0)
  const router = useRouter()

  // Check authentication
  useEffect(() => {
    const isAuthenticated = localStorage.getItem('adminAuthenticated')
    if (!isAuthenticated) {
      router.push('/admin/login')
      return
    }
  }, [router])

  // Fetch requests
  const fetchRequests = async () => {
    try {
      const response = await fetch('/api/admin/requests')
      const data = await response.json()
      
      if (response.ok) {
        setRequests(data.requests)
        
        // Check for new requests and show notification
        if (lastRequestCount > 0 && data.requests.length > lastRequestCount) {
          const newRequestsCount = data.requests.length - lastRequestCount
          showNotification(`${newRequestsCount} new request(s) received!`)
        }
        
        setLastRequestCount(data.requests.length)
      } else {
        setError(data.error || 'Failed to fetch requests')
      }
    } catch (err) {
      setError('Network error occurred')
    } finally {
      setLoading(false)
    }
  }

  // Show browser notification
  const showNotification = (message: string) => {
    if ('Notification' in window) {
      if (Notification.permission === 'granted') {
        new Notification('New Request Alert', {
          body: message,
          icon: '/favicon.ico'
        })
      } else if (Notification.permission !== 'denied') {
        Notification.requestPermission().then(permission => {
          if (permission === 'granted') {
            new Notification('New Request Alert', {
              body: message,
              icon: '/favicon.ico'
            })
          }
        })
      }
    }
  }

  // Generate PDF function
  const generatePDF = async (request: RequestData) => {
    try {
      // Dynamic import of jsPDF
      const { jsPDF } = await import('jspdf')
      const doc = new jsPDF()
      
      // Set font
      doc.setFont('helvetica')
      
      // Title
      doc.setFontSize(20)
      doc.text('REQUEST LETTER', 105, 30, { align: 'center' })
      
      // Date
      doc.setFontSize(12)
      doc.text(`Date: ${new Date(request.date).toLocaleDateString()}`, 20, 50)
      
      // Recipient
      doc.setFontSize(14)
      doc.text('To:', 20, 70)
      doc.setFontSize(12)
      const recipientLines = doc.splitTextToSize(request.recipientName, 170)
      doc.text(recipientLines, 20, 80)
      
      const recipientAddressLines = doc.splitTextToSize(request.recipientAddress, 170)
      doc.text(recipientAddressLines, 20, 90)
      
      // Subject
      doc.setFontSize(14)
      doc.text('Subject:', 20, 120)
      doc.setFontSize(12)
      const subjectLines = doc.splitTextToSize(request.subject, 170)
      doc.text(subjectLines, 20, 130)
      
      // Body
      doc.setFontSize(12)
      const bodyLines = doc.splitTextToSize(request.requestBody, 170)
      doc.text(bodyLines, 20, 150)
      
      // Sender
      const senderY = 150 + (bodyLines.length * 5) + 20
      doc.text('Sincerely,', 20, senderY)
      doc.text(request.senderName, 20, senderY + 20)
      doc.text(request.senderContact, 20, senderY + 30)
      
      const senderAddressLines = doc.splitTextToSize(request.senderAddress, 170)
      doc.text(senderAddressLines, 20, senderY + 40)
      
      // Save the PDF
      doc.save(`request-letter-${request.id}.pdf`)
      
    } catch (error) {
      console.error('Error generating PDF:', error)
      alert('Error generating PDF. Please try again.')
    }
  }

  // Logout function
  const handleLogout = () => {
    localStorage.removeItem('adminAuthenticated')
    localStorage.removeItem('adminLoginTime')
    router.push('/admin/login')
  }

  // Initial fetch and polling setup
  useEffect(() => {
    fetchRequests()
    
    // Set up polling every 30 seconds
    const interval = setInterval(fetchRequests, 30000)
    
    // Request notification permission
    if ('Notification' in window && Notification.permission === 'default') {
      Notification.requestPermission()
    }
    
    return () => clearInterval(interval)
  }, [lastRequestCount])

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Admin Dashboard</h1>
            <p className="text-gray-600">Manage request letters</p>
          </div>
          <div className="flex items-center space-x-4">
            <Badge variant="outline" className="text-sm">
              {requests.length} Total Requests
            </Badge>
            <Button 
              onClick={handleLogout}
              variant="outline"
              className="border-red-200 text-red-600 hover:bg-red-50"
            >
              Logout
            </Button>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-4 py-8">
        {error && (
          <Alert className="mb-6 border-red-500 bg-red-50">
            <AlertDescription className="text-red-700">
              {error}
            </AlertDescription>
          </Alert>
        )}

        {requests.length === 0 ? (
          <Card>
            <CardContent className="text-center py-12">
              <p className="text-gray-500 text-lg">No requests submitted yet</p>
              <p className="text-gray-400 mt-2">New requests will appear here automatically</p>
            </CardContent>
          </Card>
        ) : (
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {requests.map((request) => (
              <Card key={request.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <CardTitle className="text-lg">{request.subject}</CardTitle>
                    <Badge 
                      variant={request.category === 'urgent' ? 'destructive' : 'secondary'}
                      className="ml-2"
                    >
                      {request.category}
                    </Badge>
                  </div>
                  <CardDescription>
                    From: {request.senderName}
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="text-sm text-gray-600">
                    <p><strong>To:</strong> {request.recipientName}</p>
                    <p><strong>Date:</strong> {new Date(request.date).toLocaleDateString()}</p>
                    <p><strong>Submitted:</strong> {new Date(request.submittedAt).toLocaleString()}</p>
                  </div>
                  
                  <div className="text-sm">
                    <p className="font-medium text-gray-700">Request:</p>
                    <p className="text-gray-600 line-clamp-3">
                      {request.requestBody.length > 100 
                        ? `${request.requestBody.substring(0, 100)}...` 
                        : request.requestBody
                      }
                    </p>
                  </div>

                  <div className="text-sm text-gray-600">
                    <p><strong>Contact:</strong> {request.senderContact}</p>
                  </div>
                  
                  <Button 
                    onClick={() => generatePDF(request)}
                    className="w-full bg-black hover:bg-gray-800 text-white"
                  >
                    Generate PDF
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}
